:root {
  --background: #ffffff;
  --foreground: #171717;
  --header-bg: #ffffff;
  --header-shadow: rgba(0,0,0,0.04);
  --card-bg: #ffffff;
  --card-shadow: rgba(0,0,0,0.06);
  --border-color: #aaa;
  --text-muted: #666666;
  --button-bg: #276fda;
  --button-text: #ffffff;
}
.dark-theme {
  --background: #0f0f23;
  --foreground: #e6e6e6;
  --header-bg: #1a1a2e;
  --header-shadow: rgba(138, 43, 226, 0.2);
  --card-bg: #16213e;
  --card-shadow: rgba(138, 43, 226, 0.15);
  --border-color: #6a5acd;
  --text-muted: #b19cd9;
  --button-bg: #8a2be2;
  --button-text: #ffffff;
}
body {
  margin: 0;
  font-family: 'Arial', Helvetica, sans-serif;
  background: var(--background);
  color: var(--foreground);
  transition: background-color 0.3s ease, color 0.3s ease;
}
.home-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}
.welcome-section {
  text-align: center;
  padding: 4rem 0;
  margin-bottom: 4rem;
}
.welcome-title {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  color: var(--foreground);
}
.welcome-description {
  font-size: 1.25rem;
  color: var(--text-muted);
  max-width: 600px;
  margin: 0 auto 2rem;
  line-height: 1.6;
}
.analyze-button {
  display: inline-block;
  background: var(--button-bg);
  color: var(--button-text);
  padding: 1rem 2rem;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.analyze-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(39, 111, 218, 0.3);
}
.features-section {
  margin-top: 4rem;
}
.features-title {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 2rem;
  color: var(--foreground);
}
.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}
.feature-card {
  background: var(--card-bg);
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 12px var(--card-shadow);
  transition: transform 0.2s ease;
}
.feature-card:hover {
  transform: translateY(-4px);
}
.feature-card h3 {
  color: var(--button-bg);
  margin-bottom: 1rem;
  font-size: 1.25rem;
}
.feature-card p {
  color: var(--text-muted);
  line-height: 1.6;
}
.documentation-container,
.process-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
}
.doc-header,
.process-header {
  text-align: center;
  margin-bottom: 3rem;
}
.doc-header h1,
.process-header h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: var(--foreground);
}
.doc-header p,
.process-header p {
  font-size: 1.1rem;
  color: var(--text-muted);
}
.doc-content,
.process-content {
  line-height: 1.6;
}
.doc-section,
.process-section {
  margin-bottom: 3rem;
  padding: 2rem;
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: 0 2px 8px var(--card-shadow);
}
.doc-section h2,
.process-section h2 {
  color: var(--button-bg);
  margin-bottom: 1rem;
  font-size: 1.5rem;
}
.doc-section p,
.process-section p {
  color: var(--text-muted);
  margin-bottom: 1rem;
}
.doc-section em,
.process-section em {
  color: var(--button-bg);
  font-style: italic;
}
.analyze-card {
  background: var(--card-bg);
  border-radius: 16px;
  box-shadow: 0 4px 16px var(--card-shadow);
  margin: 2rem auto;
}
.result-card {
  background: var(--card-bg);
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 2px 8px var(--card-shadow);
  min-width: 400px;
}
.result-card h3 {
  color: var(--button-bg);
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}
.class-info-card {
  background: rgba(39, 111, 218, 0.1);
  border-left: 4px solid var(--button-bg);
  padding: 1rem;
  margin: 1rem 0;
  border-radius: 4px;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background: var(--header-bg);
  box-shadow: 0 2px 8px var(--header-shadow);
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}
.logo-link {
  display: flex;
  align-items: center;
  text-decoration: none;
}
.nav {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.nav-list {
  display: flex;
  gap: 2rem;
  list-style: none;
  margin: 0;
  padding: 0;
}
.nav-link {
  text-decoration: none;
  color: var(--foreground);
  font-weight: 500;
  transition: color 0.3s ease;
}
.nav-link:hover {
  color: var(--button-bg);
}
.theme-toggle {
  background: var(--button-bg);
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease, transform 0.2s ease;
}
.theme-toggle:hover {
  transform: scale(1.1);
}
.theme-icon {
  color: var(--button-text);
  transition: color 0.3s ease, transform 0.3s ease;
}
:root .theme-icon {
  color: #ffa500;
}
.dark-theme .theme-icon {
  color: #ffffff;
}
.main {
  padding: 2rem;
  min-height: calc(100vh - 80px);
}
.analysis-container {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  align-items: flex-start;
  gap: 2em;
  margin-bottom: 10rem;
  color: var(--foreground);
}
@media screen and (max-width: 768px) {
  .analysis-container {
    flex-direction: column;
    align-items: center;
  }
  .nav {
    gap: 1rem;
  }
  .nav-list {
    gap: 1rem;
  }
}
@property --tw-border-style {
  syntax: "*";
  inherits: false;
  initial-value: solid;
}
@layer properties {
  @supports ((-webkit-hyphens: none) and (not (margin-trim: inline))) or ((-moz-orient: inline) and (not (color:rgb(from red r g b)))) {
    *, ::before, ::after, ::backdrop {
      --tw-border-style: solid;
    }
  }
}


